import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'
import { authorizeUser } from 'entities/user';
import { toggleAuthModal } from 'pages/auth-modal/model';
import { BASE_URL } from 'shared/config';
import { LoginFormInputsState } from '../ui';

interface LoginFormSubmitResult {
  message: string;
  token: string;
  refreshToken: string;
  userId: string;
  name: string;
  error: string | null;
}

const defaultLoginFormSubmitResult: LoginFormSubmitResult = {
  message: '',
  token: '',
  refreshToken: '',
  userId: '',
  name: '',
  error: null,
}

export const submitForm = createAsyncThunk(
  'loginForm/submit', 
  async (
    formData: LoginFormInputsState,
    { dispatch }
  ) => {
  const res = await fetch(`${BASE_URL}/signin`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(formData),
  });
  let result: LoginFormSubmitResult = { ...defaultLoginFormSubmitResult };
  if (!res.ok) {
    result.error = 'Incorrect login or password';
  } else {
    result = { ...result, ...(await res.json()) };
  }
  if (!result.error) {
    const { email } = formData;
    const { token, refreshToken, userId, name } = result;
    dispatch(authorizeUser({ token, refreshToken, userId, name, email }));
    dispatch(toggleAuthModal(false));
  }
  return result;
});

export interface LoginFormState {
  status: 'idle' | 'loading' | 'succeeded' | 'failed';
  error: string | null;
  submitResult: LoginFormSubmitResult;
}

const initialState: LoginFormState = {
  status: 'idle',
  error: null,
  submitResult: { ...defaultLoginFormSubmitResult },
}

export const loginFormSlice = createSlice({
  name: 'loginForm',
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(submitForm.pending, (state, action) => {
        state.status = 'loading';
      })
      .addCase(submitForm.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.submitResult = action.payload;
      })
      .addCase(submitForm.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.error.message || '';
      })
  }
})

