import { createSlice, createAsyncThunk,  PayloadAction } from '@reduxjs/toolkit'
import { authorizeUser } from 'entities/user';
import { toggleAuthModal } from 'pages/auth-modal/model';
import { BASE_URL } from 'shared/config';
import { RegistrationFormInputsState } from '../ui';

interface RegistrationFormSubmitResult {
  email: string,
  password: string,
  name: string;
  error: {
    error: {
      status: string;
      errors: { path: string, message: string }[];
    }
  } | string | null;
}

const defaultLoginFormSubmitResult: RegistrationFormSubmitResult = {
  email: '',
  password: '',
  name: '',
  error: null,
}

export const submitForm = createAsyncThunk(
  'registrationForm/submit', 
  async (
    formData: RegistrationFormInputsState,
    { dispatch }
  ) => {
  const res = await fetch(`${BASE_URL}/users`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(formData),
  });
  let result: RegistrationFormSubmitResult = { ...defaultLoginFormSubmitResult };
  if (!res.ok) {
    const contentType = res.headers.get('Content-Type');
    const isJson = contentType?.includes('application/json');
    result.error = await (isJson ? res.json() : res.text());
  } else {
    result = { ...result, ...(await res.json()) };
  }
  if (!result.error) {
    const { name, email, password } = formData;
    const signinRes = await fetch(`${BASE_URL}/signin`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ email, password }),
    });
    const { token, refreshToken, userId } = await signinRes.json();
    dispatch(authorizeUser({ token, refreshToken, userId, name, email }));
    dispatch(toggleAuthModal(false));
  }
  return result;
});

export interface RegistrationFormState {
  status: 'idle' | 'loading' | 'succeeded' | 'failed';
  error: string | null;
  submitResult: RegistrationFormSubmitResult;
}

const initialState: RegistrationFormState = {
  status: 'idle',
  error: null,
  submitResult: { ...defaultLoginFormSubmitResult },
}

export const registrationFormSlice = createSlice({
  name: 'registrationForm',
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(submitForm.pending, (state, action) => {
        state.status = 'loading';
      })
      .addCase(submitForm.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.submitResult = action.payload;
      })
      .addCase(submitForm.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.error.message || '';
      })
  }
})
