import { configureStore } from '@reduxjs/toolkit';
import { wordsSlice } from 'entities/word';
import { authModalSlice } from 'pages/auth-modal/model';
import { loginFormSlice } from 'features/auth/login';
import { TypedUseSelectorHook, useSelector } from 'react-redux';
import { registrationFormSlice } from 'features/auth/registration';
import { userSlice, UserState, authorizeUser } from 'entities/user';

export const store = configureStore({
  reducer: {
    [wordsSlice.name]: wordsSlice.reducer,
    [authModalSlice.name]: authModalSlice.reducer,
    [loginFormSlice.name]: loginFormSlice.reducer,
    [registrationFormSlice.name]: registrationFormSlice.reducer,
    [userSlice.name]: userSlice.reducer,
  }
});

const userRawData = localStorage.getItem('user');
if (userRawData) {
  const user: UserState = JSON.parse(userRawData);
  if (user.isAuthorized) {
    store.dispatch(authorizeUser(user.data));
  }
}

store.subscribe(() => {
  const { user } = store.getState();
  localStorage.setItem('user', JSON.stringify(user));
});

export type RootState = ReturnType<typeof store.getState>;

export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;
export type AppDispatch = typeof store.dispatch;
